interface IStyleProps { style?: string; }
export const SvgIcon = ({
  vLen = 20,
  path,
  style = "text-blue-500"
}: {
  vLen?: number,
  path:  TPathDataKey    // path name defined in paths.json
} & IStyleProps) =>
  <svg {...viewBox(vLen)} {...cLo("w-full h-full fill-current", style)}>
    <path d={getPathD(path)} />
  </svg>;
