import {
  useRef,
  useEffect,
} from 'react';
import { useAuthCtx }     from 'app/AuthContext';
import { useAppDispatch } from 'app/store';
import { useSelector }    from 'react-redux';
import {
  selectAllMsgs,
  sendMsg,
} from 'features/msgs/msgsSlice';
import {
  SvgIconPhone,
  SvgIconCamcoder,
  SvgIconInfo,
  borderColor,
  AvatarImg,
} from './chat-gadgets';
import {
  cCo, cLo,
} from 'utils/util';
import ChatBubble from './ChatBubble';
import ChatInputControl from './ChatInput';

const getUserIndex = (_uid: string) => {
  // -1 when not found
  // return TESTDATA.UsersList.findIndex(({ id }) => (id === uid));
  return 0;
}

const avatarSrc = (_idx: number) => {
  // return (idx >= 0) ? TESTDATA.UsersList[idx].avatar : "";
  return "https://randomuser.me/api/portraits/men/9.jpg";
}

// tailwind colorset
const tcsBgBorder = "dark:border-gray-600 dark:bg-gray-800 border-gray-400 bg-gray-300";

const ChatHead = () => {
  const urlDest = "http://localhost:3000";
  const linkedIcons = [
    {  href: urlDest, icon: <SvgIconPhone /> },
    {  href: urlDest, icon: <SvgIconCamcoder /> },
    {  href: urlDest, icon: <SvgIconInfo /> },
  ];
  const nUsers = 3;
  const nMsgs  = 20;

  return (
    <div
      {...cLo("flex flex-row items-center justify-between flex-none px-2 shadow border-b",
          tcsBgBorder)}
    >
      <div className="flex">
        <AvatarImg url={"https://scx2.b-cdn.net/gfx/news/hires/2009/spacetime.jpg"} />
        <div className="text-sm">
          <p className="font-bold">{"room.title"}</p>
          <p className="text-xs">
            <span className="font-bold dark:text-yellow-400 text-yellow-700">{nUsers}</span> users{' '}
            <span className="font-bold dark:text-blue-400 text-blue-700">{nMsgs}</span> messages
          </p>
        </div>
      </div>
      <div className="flex">
        {linkedIcons.map((li, i) =>
          <a
            key={i}
            href={li.href}
            {...cLo('block w-6 h-6 p-1 rounded-full dark:bg-gray-800 dark:hover:bg-gray-600',
              "hover:bg-gray-300",
              (i > 0) && 'ml-1')}
          >
            {li.icon}
          </a>
        )}
      </div>
    </div>
  );
}

const ChatBody = () => {
  const msgs = useSelector(selectAllMsgs);
  const refW = useRef<HTMLDivElement>(null);  // for scroll control
  const sender = useAuthCtx().user!.email!;
  const dispatch = useAppDispatch();

  useEffect(() => {
    const node = refW.current;
    if (node) {
      node.scrollTop = node.scrollHeight;
    }
    // console.log("Scrolled to the bottom.");
  }); //, []);  // Run only initially once

  const onNewMsg = (text: string) => {
    const Commands = ['/', ':'];
    const isCommand = (text && Commands.includes(text[0]));
    if (isCommand) {
      console.log(text.substring(1));
    } else {
      dispatch( sendMsg({ sender, text }) );
    }
  }

  let idPrev: string0 = null;

  return (
    <>
      <div ref={refW} className="flex-1 p-4 overflow-y-scroll">
        {msgs.map((m, i) => {
          const idSelf  = m.sender === sender;
          const isNewId = m.sender !== idPrev;
          if (isNewId)
            idPrev = m.sender;  // save the current id to check the changes at the next loop
          const idx = getUserIndex(m.sender);
          const bdrClr = borderColor(idx);
          return (
            <div key={i} {...cCo("flex flex-row", idSelf, "justify-end", "justify-start")} >
              {!idSelf && <AvatarImg url={avatarSrc(idx)} show={isNewId} bdrClr={bdrClr} />}
              <ChatBubble {...{m, idSelf, isNewId, bdrClr}} />
            </div>
          );
        })}
      </div>
      <ChatInputControl onSubmit={onNewMsg} />
    </>
  );
}

export default function ChatPane() {
  return (
    <>
      <ChatHead />
      <ChatBody />
    </>
  );
}
