import {
  createSlice,
  createAsyncThunk,
  createEntityAdapter,
} from '@reduxjs/toolkit';
import { TRootState } from 'app/store';
import {
  IMsg,
  IMsgSend
} from 'common';
import {
  addMsgAPI
} from 'api/dataAPI';
import { store } from 'app/store';

const msgsAdapter = createEntityAdapter<IMsg>({
  selectId: msg => msg.id,  // default behavior
  sortComparer: (a, b) => (b.time < a.time) ? 1 : ((b.time > a.time) ? -1 : 0)  // ascending order
});

const initialState = msgsAdapter.getInitialState({
  status: 'idle',
  error: '',
  roomId: '',
});

// For the context where useSelector(selectRoomId) cannot be used.
const getRoomId = () => store.getState().msgs.roomId;

// create User by id (document id = user.uid)
export const sendMsg = createAsyncThunk<
  void,      // Return type    of the payloadCreator
  IMsgSend   // First argument to the payloadCreator
>(
  'msgs/sendMsg',  // type
  async (msg: IMsgSend) => {  // payloadCreator
    console.log("sendMsg:", msg);  // Can't use useSelector hook since it's not a React function component,
    const roomId = getRoomId();
    // if (!roomId)
    //   return;
    return await addMsgAPI(roomId, msg); // ******************* API call
  }
);

const msgsSlice = createSlice({
  name: 'msgs',
  initialState,
  reducers: {
    enterRoom(state, action) {
      state.roomId = action.payload;
    },
    msgAddOne:    msgsAdapter.addOne,
    msgUpsertOne: msgsAdapter.upsertOne,
    msgSetAll:    msgsAdapter.setAll,
    msgRemoveOne: msgsAdapter.removeOne,
    msgRemoveAll: msgsAdapter.removeAll,
    // sendMsg(state, action) {
    //   msgsAdapter.addOne(state, action);
    // },
  },
  extraReducers: builder => {
    builder
    .addCase(sendMsg.fulfilled, (state, _action) => {
      state.status = 'succeeded';  // update data will be received later
    })
    .addCase(sendMsg.rejected, (state, action) => {
      state.status = 'failed';
      state.error = action.error.message || "sendMsg error!";
    })
  }
});

export default msgsSlice.reducer;
export const {
  enterRoom,
  msgAddOne,
  msgUpsertOne,
  msgSetAll,
  msgRemoveOne,
  msgRemoveAll,
} = msgsSlice.actions;

export const {
  selectAll:  selectAllMsgs,
  selectById: selectMsgById,
  selectIds:  selectMsgIds,
} = msgsAdapter.getSelectors((state: TRootState) => state.msgs);
export const selectMsgStatus = (state: TRootState) => state.msgs.status;
export const selectRoomId    = (state: TRootState) => state.msgs.roomId;
