const RoomsList = () => {
  const RoomsList = [
    {
      "id": "tprYAJxY2blNg1X1cT5z",
      "title": "First Room",
      "img": "https://www.flaticon.com/premium-icon/icons/svg/2179/2179306.svg",
      "timestamp": "2020-06-11T13:25:43.511Z",
    },
    {
      "id": "HgOCHACGcol4jj3gxoLW",
      "title": "STQ Dev Session",
      "img": "https://www.flaticon.com/svg/vstatic/svg/166/166908.svg?token=exp=1612135303~hmac=61afc7003feb9745d6ccb5f3e35bcb21",
      "timestamp": "2021-01-31T09:12:11.511Z",
    },
  ];
  const CONTEXTMENU = 'contextmenu';
  const ref = useRef<HTMLDivElement>(null);
  const curRoomId = useSelector(selectRoomId);
  const dispatch = useAppDispatch();

  useEffect(() => {
    const handleContextMenu: EventListener = (e) => {
      e.preventDefault();
      console.log(CONTEXTMENU);
    }
    const el = ref.current;
    el?.addEventListener(CONTEXTMENU, handleContextMenu);
    return () => {
      el?.removeEventListener(CONTEXTMENU, handleContextMenu);
    }
  }, []);

  const onClickHandle = (roomId: string) => {
    if (roomId !== curRoomId)
      dispatch( enterRoom(roomId) );
  }

  return (
    <div ref={ref} className="flex-1 p-2 overflow-y-scroll contacts">
      {RoomsList.map((rm, i) =>
        <div
          key={rm.id}
          {...cLo("relative flex items-center justify-between p-1 cursor-pointer",
            "dark:hover:bg-gray-800 hover:bg-gray-300",
            (rm.id === curRoomId) && "dark:bg-blue-600 bg-blue-200 dark:hover:bg-blue-800 hover:bg-blue-300")}
          onClick={() => onClickHandle(rm.id)}
        >
          <AvatarImg url={rm.img} size="w-12 h-12" bdrClr={borderColor(i)} />
          <div className="flex-auto hidden min-w-0 mx-1 md:block group-hover:block">
            {rm.title}
            <div className="flex items-center text-sm dark:text-gray-100 text-gray-700">
              <p className="ml-2 text-xs whitespace-nowrap dark:text-green-400 text-green-700">
                {timeStr(rm.timestamp)}
              </p>
            </div>
          </div>
        </div>
      )}
    </div>
  );
}

export default function LeftPane() {
  return (
    <>
      <LeftPaneHead>Members</LeftPaneHead>
      <RoomUsersList />
      <LeftPaneHead>Sessions</LeftPaneHead>
      <RoomsList />
    </>
  );
}
