import {
  BrowserRouter,
  Switch,
} from "react-router-dom";
import {
  IAuthRouteProps,
  ARoute,
  AuthRedirectToFrom,
} from 'ui/AuthRoute';
import { useAuthCtx } from 'app/AuthContext';
import Navbar    from './Navbar';
import AuthPage  from 'features/auth/AuthPage';
import Dashboard from 'features/dash/Dashboard';
import Calendar  from 'features/cal/Calendar';
import Timeline  from 'features/timeline/Timeline';
import Chat      from 'features/chat/Chat';
import Profile   from 'features/profile/ProfilePage';
import Setting   from 'features/setting/SettingPage';

export default function Layout() {
  // react-router: component and render cannot be used at the same time
  // SubRoutes not supported yet.
  const Routes: Partial<IAuthRouteProps>[] =  [  // access level: 0 public, 1: login required
    { path: "/chat",     children: <Chat />,        access: 1 },
    { path: "/calendar", children: <Calendar />,    access: 0 },
    { path: "/timeline", children: <Timeline />,    access: 0 },
    { path: "/auth",     children: <AuthPage />,    access: 0 },
    { path: "/profile",  children: <Profile />,     access: 1 },
    { path: "/setting",  children: <Setting />,     access: 1 },
    { path: "/",         children: <Dashboard />,   access: 0 },
  ];
  // const auth = useSelector(selectAuth);  // mystery: this call destroys auth reducer!
  const user = useAuthCtx().user;
  return (
    <BrowserRouter>
      <Navbar />
      <AuthRedirectToFrom user={user} />
      <Switch>
        {
        Routes.map((rt, i) => <ARoute key={i} authLv={user ? 1 : 0} {...rt} />)
        }
      </Switch>
    </BrowserRouter>
  );
}
