import { useState } from 'react';
import { cLo } from 'utils/util';
import { squareView, getPathD } from 'utils/svg';
import { IMouseProps } from 'common';

// tailwind colorset
const tcsText = "dark:text-white text-black";
const tcsBorder = "dark:border-gray-600 border-gray-300";

const CalTHead = () => {
  const week = [  // week name and additional attributes
    { name: "Sunday",    attr: "dark:text-red-400 text-red-500 font-bold" },
    { name: "Monday",    attr: tcsText},
    { name: "Tuesday",   attr: tcsText},
    { name: "Wednesday", attr: tcsText},
    { name: "Thursday",  attr: tcsText},
    { name: "Friday",    attr: tcsText},
    { name: "Saturday",  attr: "dark:text-blue-400 text-blue-500 font-bold" }
  ];

  return (
    <thead>
      <tr>
        {week.map((wk, i) =>
          <th
            key={i}
            {...cLo("w-10 h-10 p-2 text-xs border xl:w-40 lg:w-30 md:w-30 sm:w-20 xl:text-sm",
              tcsBorder, "dark:bg-gray-800 bg-gray-200")}
          >
            <span {...cLo("hidden xl:block lg:block md:block sm:block",    wk.attr)}>{wk.name}</span>
            <span {...cLo("block xl:hidden lg:hidden md:hidden sm:hidden", wk.attr)}>{wk.name.substr(0,3)}</span>
          </th>
        )}
      </tr>
    </thead>
  );
}

