const CalTBody = () => {
  //const lns = [];

  // category, name, time
  type TEventInfo = Record<'cat' | 'name' | 'time', string>;

  type TDateInfo = {
    date:  number;
    evList?: TEventInfo[];
  };

  const WeekRow = ({ aDI }: {
    aDI: TDateInfo[]
  }) => {
    return (
      <tr className="h-20 text-center">
        {aDI.map((di, i) =>
        <td
          key={i}
          {...cLo("w-10 h-40 p-1 overflow-auto border cursor-pointer xl:w-40 lg:w-30 md:w-30 sm:w-20", tcsBorder,
            "transition duration-500 ease dark:hover:bg-gray-500 hover:bg-gray-300")}
        >
          <div className="flex flex-col w-10 h-40 mx-auto overflow-hidden xl:w-40 lg:w-30 md:w-30 sm:w-full">
            <div className="w-full h-5 top">
              <span className="dark:text-gray-200 text-gray-500">{di.date}</span>
              {di.evList &&
                di.evList.map((ev, j) => <EventNote key={j} event={ev} />)
              }
            </div>
          </div>
        </td>
        )}
      </tr>
    );
  }

  const WEEK_DATA: TDateInfo[][] = [
    [
      { date: 30 },
      { date: 31 },
      { date:  1 },
      { date:  2 },
      { date:  3, evList: [ { cat: "A", name: "Show", time: "12:00~14:00" } ] },
      { date:  4 },
      { date:  5 },
    ],
    [
      { date:  6 },
      { date:  7, evList: [ { cat: "B", name: "MT", time: "12:00~14:00" },
                            { cat: "C", name: "MT", time: "15:00~16:00" },
                            { cat: "D", name: "MT", time: "16:00~18:00" } ] },
      { date:  8 },
      { date:  9 },
      { date: 10 },
      { date: 11 },
      { date: 12 },
    ],
    [
      { date: 13 },
      { date: 14 },
      { date: 15 },
      { date: 16 },
      { date: 17, evList: [ { cat: "D", name: "TV", time: "14:00~16:00" } ] },
      { date: 18 },
      { date: 19 },
    ],
    [
      { date: 20 },
      { date: 21 },
      { date: 22, evList: [ { cat: "E", name: "Voting", time: "12:00~14:00" } ] },
      { date: 23 },
      { date: 24 },
      { date: 25 },
      { date: 26 },
    ],
    [
      { date: 27 },
      { date: 28 },
      { date: 29 },
      { date: 30 },
      { date:  1 },
      { date:  2 },
      { date:  3 },
    ],
  ];

  const EventNote = ({ event }: { event: TEventInfo }) => {
    const palette = () => {
      switch (event.cat) {
        case 'A': return 'bg-purple-400';
        case 'B': return 'bg-purple-700';
        case 'C': return 'bg-green-400';
        case 'D': return 'bg-blue-400';
        case 'E': return 'bg-yellow-500';
        default:  return 'bg-gray-400';
      }
    };
    return (
      <div {...cLo("p-1 mb-1 text-sm text-white rounded-lg", palette())}>
        <span className="font-bold">{event.name}</span>
        <span className="text-xs text-blue-100">{' '}{event.time}</span>
      </div>
    );
  }

  return (
    <tbody>
      {WEEK_DATA.map((wk, i) => <WeekRow key={i} aDI={wk} />)}
    </tbody>
  );
}

// With the left arrow, generate 4 directional arrows
const ButtonArrow = ({ dir, onClick }: {
  dir: 'L' | 'R' | 'U' | 'D'  // Left, Right, Up, Down
} & IMouseProps) => {
  const angle = (dir === 'L')
    ? 0
    : (dir === 'R')
      ? 180
      : (dir === 'U')
        ? 90
        : -90; // Up, Down not implemented yet
  return (
    <button className="p-1 hover:text-yellow-400" onClick={onClick}>
      <svg
        {...squareView("1.2rem", 16)} className="fill-current"
        transform={`rotate(${angle})`}
      >
        <path fillRule="evenodd" d={getPathD("arrow_circle")} />
      </svg>
    </button>
  );
}

export default function Calendar() {
  // month count from the current month
  const [month, setMonth] = useState(1);
  const mTest = [
    "January 2021",
    "February 2021",
    "March 2021",
    "April 2021",
  ];
  return (
    <div {...cLo("flex items-center justify-center h-screen overflow-y-scroll",
      "dark:bg-gray-800 bg-gray-200")}
    >
      <div className="flex items-start justify-center w-screen h-screen p-2">
        <div className="container w-full">
          <div
            {...cLo("flex justify-between p-1 mt-2 rounded-t-xl",
              "dark:text-gray-900 dark:bg-gray-200 text-gray-50 bg-gray-800")}
          >
            <span {...cLo("ml-3 text-xl font-bold")}>
              {mTest[Math.abs(month % mTest.length)]}
            </span>
            <div>
              <ButtonArrow dir='L' onClick={(_e) => setMonth(month - 1)}/>
              <ButtonArrow dir='R' onClick={(_e) => setMonth(month + 1)}/>
            </div>
          </div>
          <table className="w-full dark:bg-gray-800 bg-gray-50 mb-2">
            <CalTHead />
            <CalTBody />
          </table>
        </div>
      </div>
    </div>
  );
}
