import {
  NavLink,
} from 'react-router-dom';
import { useAuthCtx }     from 'app/AuthContext';
import { TAccessLevel }   from 'ui/AuthRoute';
import {
  IMenuItemClickProps
} from 'common';
import { cC0o, cCo, cL, cLo } from 'utils/util';
import { LogoutButton } from 'features/auth/AuthForms';

// Menu items have two types of links:
// 1. Route causes the page(url) change - to: url
// 2. command with no page change       - title: used as a command
const MENU_SIGNOUT = 'signout';
interface IMenuItem {
  access: TAccessLevel;
  to?   : string;
  title : string;
};
const USER_MENUITEMS: IMenuItem[] =
[
  { access: 0, to: "/auth",    title: "Sign in", },  // when signed-out
  { access: 1, to: "/profile", title: "Profile", },  // when signed-in
  { access: 1, to: "/setting", title: "Setting", },  //
  { access: 1, title: MENU_SIGNOUT, }
];

const ProfileImg = ({ img }: { img: string0 }) =>
  <img
    {...cCo("w-8 h-8 rounded-full", img, "border border-yellow-400", "bg-gray-200")}
    src={img || 'icon-anonymous.png'}
    alt="avatar"
  />;

// Popup Menu - display on hover: with CSS classes Dropdown, Dropdown-menu
export const UserMenu = () => {
  const user = useAuthCtx().user;
  return (
    <div className="relative ml-3 Dropdown">
      <button
        {...cLo("flex text-sm border-2 border-transparent rounded-full transition",
          "duration-150 ease-in-out hover:border-yellow-500 focus:outline-none focus:border-white")}
        id="user-menu"
        aria-label="User menu"
        aria-haspopup="true"
      >
        <ProfileImg img={user && user.photoURL} />
      </button>
      <div className="Dropdown-menu hidden absolute right-0 z-40 w-32 rounded-full shadow-lg origin-top-right">
        <UserMenuList popup accessLv={user ? 1 : 0} />
      </div>
    </div>
  );
}

// Used in Navbar.tsx
export const UserMenuListPulldown = (props: IMenuItemClickProps) => {
  const user = useAuthCtx().user;
  return (
    <div className="pb-3 border-t border-gray-700">
      { user &&
      <div className="flex items-center pt-3 px-5 space-x-3">
        <div className="flex-shrink-0">
          <ProfileImg img={user.photoURL} />
        </div>
        <div className="space-y-1 font-medium leading-none">
          <div className="text-base dark:text-white text-black">{user.name}</div>
          <div className="text-sm dark:text-gray-400 text-gray-600">{user.email}</div>
        </div>
      </div>
      }
      <UserMenuList accessLv={user ? 1 : 0} {...props} />
    </div>
  );
}

// Disable the menuitem when it matches the current URL, using <NavLink> rather than <Link>.
// Show menuitems according to the access level
const UserMenuList = ({ popup, accessLv }: {
  popup?: boolean,
  accessLv: 0 | 1;
}) => {
  return (
    <div
      {...cC0o(popup,
        "py-1 bg-white rounded-md shadow-xs",
        "px-2 mt-3 space-y-1")}
      role="menu"
      aria-orientation="vertical"
      aria-labelledby="user-menu"
    >
      {USER_MENUITEMS.filter(a => a.access === accessLv).map((m, i) =>
        {return (m.to)
        ? <NavLink
            key={i}
            exact to={m.to}
            {...cC0o(popup,
              "block px-4 py-2 text-sm font-bold text-gray-700 hover:bg-gray-100",
              cL("block px-3 py-1 text-base font-medium rounded-md dark:text-gray-300 text-gray-800",
                "hover:text-white hover:bg-gray-700 focus:outline-none focus:text-white focus:bg-gray-700"))}
            activeStyle={{
              pointerEvents: 'none',
              fontStyle: 'italic',
              color: 'darkgray'
            }}
            role="menuitem"
          >
            {m.title}
          </NavLink>
        : <MenuCommands key={i} popup={popup} command={m.title} />
      })}
    </div>
  );
}

const MenuCommands = ({ popup, command }: {
  popup?:  boolean,
  command: string,
}) => {
  switch (command) {
    case MENU_SIGNOUT:
      return (
        <LogoutButton
          {...cCo("px-4", popup, "w-full", "rounded-md bg-gray-200")}
        >
          Sign Out
        </LogoutButton>
      );
    default:
      return null;
  }
}
