interface IExButtonProps extends Partial<IButtonProps>, IClassNameObj {};
export const ButtonPurple: React.FC<IExButtonProps> = (props) => {
  const { className: cn, ...rest } = props;  // cn will be added only when it's a valid string
  return (
    <button
      {...cLo("flex-shrink-0 bg-purple-600 text-base font-bold py-2 px-4",
        "rounded-lg shadow-md hover:bg-purple-700 focus:outline-none focus:ring-2",
        "focus:ring-purple-500 focus:ring-offset-2 focus:ring-offset-purple-200 text-white", cn)}
      {...rest}
    />
  );
}

// Extract the behavior to close(hide) on click as a hook
// When you need to keep the layout and just hide the window, use "invisible"
// - Dependencies: .Ani_dialog / .Ani_dialog_close in App.scss
// Usage) const { display, handleClose } = useClose("opacity-50");
// <div {...cLo("...", display)} onClick={handleClose} >
type TOpVariant = 0 | 5 | 10 | 20 | 25 | 30 | 40 | 50 | 60 | 70 | 75 | 80 | 90 | 95 | 100;
export type THideAttr =
  | "hidden"
  | "invisible"
  | `opacity-${TOpVariant}`
  | "Ani";
export const useClose = (hideAttr: THideAttr = "Ani") => {
  const [close, setClose] = useState(false);
  const handleClose = () => {
    setClose(!close);
  }
  return {
    hide: (hideAttr === "Ani")
      ? `Ani_dialog${close ? "_close" : ""}`
      : (close ? hideAttr : ""),
    handleClose
  };
}

export type TIconKind = "error" | "ok" | "warn" | "info";
export type TIconKindColors = { [K in TIconKind]:  stringU };
// IconButton on the dialog
// role="button" shows cursor-pointer
interface IIconButtonProps extends Partial<IClassNameObj> { // add height, width, text and border colors
  kind:    TIconKind;   // kind of the icon image
  onClick: () => void;  // callback to close(hide) parent
};
export const IconButton = ({
  kind,
  className = "h-10 w-10 border-4",
  onClick
}: IIconButtonProps) => {
  const colors: TIconKindColors = {
    error:
      cL("text-red-500 border-red-500 hover:text-red-700 hover:border-red-700",
         "dark:text-yellow-400 dark:border-yellow-400 dark:hover:text-white dark:hover:border-white"),
    ok:
      cL("text-green-500 border-green-500 hover:text-green-700 hover:border-green-700",
         "dark:text-white dark:border-white dark:hover:text-white dark:hover:border-white"),
    warn:
      cL("text-yellow-500 border-yellow-500 hover:text-yellow-700 hover:border-yellow-700",
         "dark:text-white dark:border-white dark:hover:text-white dark:hover:border-white"),
    info:
      cL("text-blue-500 border-blue-500 hover:text-blue-700 hover:border-blue-700",
         "dark:text-white dark:border-white dark:hover:text-white dark:hover:border-white"),
  };
  return (
    <div
      {...cLo("flex items-center justify-center flex-shrink-0 rounded-full hover:shadow-md",
        "transition duration-300 ease-in-out transform hover:scale-125",
        colors[kind], className)}
      role="button"
      onClick={onClick}
    >
      <svg fill="currentColor" {...viewBox(20)} className="w-6 h-6">
        <path {...pathRules} d={getPathD(("ico_" + kind) as TPathDataKey)} />
      </svg>
    </div>
  );
}
/* &times; */

export const CloseButton = ({
  className = "h-4 w-4 border",
  onClick
}: IIconButtonProps) => {
  return (
    <IconButton
      kind="error"
      {...cLo("absolute top-0 right-0 mt-1 mr-1 opacity-50 hover:opacity-100", className)}
      onClick={onClick}
    />
  );;
}

