import { useSelector } from 'react-redux';
import { TRootState } from 'app/store';
import { useAuthCtx } from 'app/AuthContext';
import {
  currentUser,
} from 'api/authAPI';
import {
  createUser,
  selectUserById,
} from 'features/users/usersSlice';
import {
  apiReq
} from 'features/auth/authSlice';
import { useAppDispatch } from 'app/store';
import {
  ButtonPurple,
} from 'ui/ui';
import {
  getPathD,
} from 'utils/svg';
import { format, formatDistanceToNow } from 'date-fns';

const Register = () => {
  const dispatch = useAppDispatch();
  const user = useAuthCtx().user;
  return (
    <div className="mt-4 w-80 max-w-sm bg-white shadow-lg rounded-lg overflow-hidden">
      <div className="font-semibold text-lg text-center">
        <ButtonPurple className="w-60 m-2" onClick={() => dispatch( createUser(user!) )}>
          Register
        </ButtonPurple>
      </div>
    </div>
  );
}

const timeUMTtoLocal = (umt: Undefinable<string>) => {
  if (!umt)
    return;
  const date = new Date(umt);
  return format(date, 'yyyy-MM-dd HH:mm:ss z eee') + ', '
       + formatDistanceToNow(date) + ' ago';
}

export default function ProfilePage() {
  const cUser = currentUser();
  const ux = useSelector((state: TRootState) => selectUserById(state, cUser ? cUser.uid : '100'));
  if (!cUser) {
    return null;
  }
  return (
    <div className="h-screen overflow-hidden flex flex-col items-center justify-center">

      <div className="max-w-sm w-1/2 dark:bg-gray-300 bg-white shadow-lg rounded-lg overflow-hidden">
        <div className="dark:bg-gray-300 bg-white pb-12 w-full justify-center items-center overflow-hidden md:max-w-sm rounded-lg mx-auto">
          <div className="relative h-48">
            <img
              className="absolute h-full w-full object-cover"
              src={(ux && ux.backURL) || "shiba.jpg"}
              alt="background"
            />
          </div>
          <div className="relative shadow mx-auto h-24 w-24 -my-12 border-white rounded-full overflow-hidden border-4">
            <img
              className="w-full h-full object-cover bg-gray-200"
              src={cUser.photoURL || "icon-anonymous.png"}
              alt="avatar"
            />
          </div>
        </div>
        <div className="font-semibold text-lg text-center">
          <DisplayName name={cUser.displayName} />
        </div>
        <div className="py-4 px-6">
          <div className="text-xs font-thin font-mono text-gray-800 text-center">uid:{cUser.uid}</div>
          <p className="py-2 text-lg text-gray-700 border-b border-gray-200">{ux && ux.profile}</p>
          <div className="flex items-center mt-4 text-gray-700">
            <svg className="h-6 w-6 mr-2 fill-current" viewBox="0 0 512 512">
              <path d={getPathD("people_back") + getPathD("people_front")}/>
            </svg>
            {ux && ux.group}
          </div>
          <div className="flex items-center mt-4 text-gray-700">
            <svg className="h-6 w-6 mr-2 fill-current" viewBox="0 0 512 512">
              <path d={getPathD("pin_location")}/>
            </svg>
            {ux && ux.city}
          </div>
          <div className="flex items-center mt-4 text-gray-700">
            <svg className="h-6 w-6 mr-2 fill-current" viewBox="0 0 512 512">
              <path d={getPathD("email_pro")}/>
            </svg>
            {cUser.email} {cUser.emailVerified ? "" : <ButtonEmailVerificaiton />}
          </div>
          <div className="font-mono p-2 bg-gray-100" style={{fontSize: '.6rem'}}>
            <div className="bg-gray-600 text-gray-100 pl-1">{Intl.DateTimeFormat().resolvedOptions().timeZone}</div>
            <div className="bg-gray-300 text-red-700 pl-1">{timeUMTtoLocal(cUser.metadata.creationTime)}</div>
            <div className="bg-gray-100 text-indigo-500 pl-1">{timeUMTtoLocal(cUser.metadata.lastSignInTime)}</div>
          </div>
          <ul className="mt-1 font-mono text-xs text-gray-400">
            {cUser.providerData.map((pd, i) =>
              <ul key={i}>[Provider {i}] {pd?.providerId} / <DisplayName name={pd?.displayName} /></ul>
            )}
          </ul>
        </div>
      </div>
      {!ux && <Register />}
    </div>
  );
}

const ButtonEmailVerificaiton = () => {
  const dispatch = useAppDispatch();
  return (
    <span className="font-mono text-xs text-red-400 ml-2">
      <button
        type="button"
        className="rounded-lg border border-rose-400 px-2 hover:text-white hover:bg-rose-800"
        onClick={() => dispatch( apiReq({ command: 'sendEmailVerification' }) )}
      >
         Verify Email
      </button>
    </span>
  );
}

const DisplayName = ({ name }: { name: Maybe<string> }) =>
  name
    ? <span>{name}</span>
    : <span className="italic text-red-400">No Name</span>;
