import {
  useState,
  useRef,
} from 'react';
import {
  TKeyboardInputHandler
} from 'common';
import {
  SvgIconWrite,
  SvgIconSmile,
  SvgIconPicture,
} from './chat-gadgets';
import { cL, cLo } from 'utils/util';

export default function ChatInputControl ({ onSubmit }: {
  onSubmit: (text: string) => void
}) {
  //console.log("ChatInputControl");
  // input on focus or out of focus
  const [noCompo, setNoCompo] = useState(true);  // not in IME composition mode
  const [hasText, setHasText] = useState(false);
  // text input editor DOM element, to get textContent and clear
  const refEd = useRef<HTMLInputElement>(null);
  const btnCls = cL(
    "flex flex-shrink-0 block w-6 h-6 mx-1 focus:outline-none",
    "dark:text-gray-300 dark:hover:text-gray-600",
    "text-gray-600 hover:text-black"
  );
  // To check if in IME mode
  const handleCompositionStart = () => {
    console.log("compositionStart");
    setNoCompo(false);
  }
  const handleCompositionEnd = () => {
    console.log("compositionEnd");
    setNoCompo(true);
  }
  const onClickEmoticon = () => {
    console.log("Emoticon");
  }
  const onClickPicture = () => {
    console.log("Picture");
  }
  const _sendText = () => {
    const node = refEd.current;
    const txt = node?.textContent;
    if (txt && txt.length > 0) {
      onSubmit(txt);
      node!.innerHTML = ''; // clear current input
      setHasText(false);
    }
  }
  const submitText: TKeyboardInputHandler = (ev) => {
    ev.preventDefault();
    _sendText();
  }
  const handleKeyDown: TKeyboardInputHandler = (ev) => {
    if ((noCompo && (ev.key === "Enter")) &&
      !ev.shiftKey)  // Shift + ENTER will not end typing, next line
      return submitText(ev);
  }
  const handleKeyUp: TKeyboardInputHandler = (ev) => {
    const txt = (ev.target as HTMLInputElement).innerHTML;  // Type Assertion
    setHasText(txt.length > 0 && txt !== '\n');
  }
  return (
    <div className="flex-none">
      <div {...cLo("flex flex-row items-center p-2 border-t",
        "dark:bg-gray-800 dark:border-gray-600 bg-gray-400 border-gray-300")}
      >
        <div className="relative flex-grow">
          <form>
            <div
              {...cLo("w-full py-2 pl-3 pr-10 border rounded-md",
                "dark:text-gray-200 dark:bg-gray-800 dark:border-gray-700",
                "dark:focus:border-gray-700 dark:focus:bg-gray-900",
                "text-black bg-gray-200 border-gray-300",
                "focus:border-gray-500 focus:bg-gray-50",
                "focus:outline-none focus:shadow-md transition duration-200 ease-in")}
              ref={refEd}
              tabIndex={0}
              contentEditable="true"
              placeholder="Type in..."
              onCompositionStart={handleCompositionStart}
              onCompositionEnd={handleCompositionEnd}
              onKeyDown={handleKeyDown}
              onKeyUp={handleKeyUp}
            />
            <button
              type="button"
              {...cLo(btnCls, "absolute top-0 right-0 mt-2 mr-3", !hasText && "opacity-20")}
              onClick={_sendText}
            >
              <SvgIconWrite />
            </button>
          </form>
        </div>
        <button type="button" className={btnCls} onClick={onClickEmoticon}><SvgIconSmile /></button>
        <button type="button" className={btnCls} onClick={onClickPicture}><SvgIconPicture /></button>
      </div>
    </div>
  );
}
