import {
  cCo, cLo
} from 'utils/util';
import {
  pathRules,
  squareView,
  getPathD,
} from 'utils/svg';
import {
  IButtonProps,
} from 'common';
import { useDarkThemeCtx } from 'app/darkThemeCtx';

// slider: Change of the position by justify-end cannot be animated with transition.
// 'translate' of the slider can be animated but how can we figure out the amount?
interface IToggleSliderProps extends IButtonProps {
  on:        boolean;       // on or off
  colors?:   string0;       // button colors
  srOnly?:   string0;       // screen reader only
  size?:     TSizeVariants; // slider/ball size
  disabled?: boolean;       // disable slider
}
export const ToggleSlider: React.FC<IToggleSliderProps> = ({
  on, onClick,
  colors,
  srOnly,
  size = 'md',
  disabled,
  children,
}) => {
  const sz = {  // size dependent adjustment
    sm: { slide: "w-7 h-4",  ball: "w-3 h-3", dx: "translate-x-3" },
    md: { slide: "w-11 h-6", ball: "w-5 h-5", dx: "translate-x-5" },
    lg: { slide: "w-14 h-8", ball: "w-7 h-7", dx: "translate-x-6" },
  };
  return (
    <button
      role="switch"
      tabIndex={0}
      {...cLo("inline-flex items-center px-0.5 rounded-full", sz[size].slide,
        "transition-colors duration-200 focus:outline-none Inner-shadow", colors)}
      aria-checked={on}
      onClick={onClick}
      disabled={disabled}
    >
      {srOnly && <span className="sr-only">{srOnly}</span>}
      {children}
      <span
        {...cLo("absolute rounded-full border",
          disabled ? "border-gray-800 bg-gray-500 Ball-shadow" : "border-gray-400 bg-white Ball-shadow",
          "transition transform duration-300 ease-in-out",
          on && sz[size].dx, sz[size].ball)}
        style={{ transformOrigin: "50% 50% 0px" }}
      />
    </button>
  );
}

// Left Button switch to enable/disable "media" mode (that prefers media)
// ToggleSlider to force "dark"/"light" mode
export const ToggleDarkButton = ({
  className,
  colors    = "bg-gray-500",
  size      = 'md',
}: Partial<IClassNameObj> & Pick<IToggleSliderProps, "colors" | "size">) => {
  const { dark, media, night, toggle } = useDarkThemeCtx();  // checks both LocalStorage.theme and prefers-color-scheme
  const stopCls = "transition-colors duration-200";
  const len = {  // sun and moon icon sizes
    sm: { light: 20, dark: 16 },
    md: { light: 24, dark: 20 },
    lg: { light: 28, dark: 24 },
  };
  return (
    <span {...cLo("flex items-center", className)}>
      <button
        role="switch"
        {...cCo("w-2.5 h-2.5 rounded-full mr-1.5 Ball-shadow focus:outline-none border border-gray-500",
          media, "bg-gray-200", night ? "bg-red-500" : "bg-green-400")}
        aria-checked={!media}
        onClick={() => toggle(media)}
      />
      <ToggleSlider
        on={dark}
        onClick={() => toggle()}
        srOnly="Dark mode switch"
        colors={media ? colors + " opacity-50" : colors}
        size={size}
        disabled={media}
      >
        <svg
          {...squareView(len[size].dark, 24)}
          {...cLo(stopCls, "mx-1 dark:text-yellow-200 text-gray-500")}
          fill="currentColor"
        >
          <path {...pathRules} d={getPathD("moon_dark")} />
        </svg>
        <svg {...squareView(len[size].light, 32)} fill="none">
          <path {...pathRules} fill="url(#lG_light)" d={getPathD("sun_light")} />
          <defs>
            <linearGradient id="lG_light" x1="2" y1="2" x2="30" y2="30" gradientUnits="userSpaceOnUse">
              <stop className={stopCls} stopColor={dark ? "#d4d4d8" : "#FACC15"} />
              <stop className={stopCls} stopColor={dark ? "#d4d4d8" : "#FA9D16"} offset="1" />
            </linearGradient>
          </defs>
        </svg>
      </ToggleSlider>
    </span>
  );
}

