import {
  cC, cCo, cL, cLo,
  timeStr,
} from 'utils/util';
import {
  IMsg,
  IButtonProps,
} from 'common';
import {
  SvgIconReply,
  SvgIconSmile,
} from './chat-gadgets';

const ChatReactions = () => {
  // SvgIcon Wrapper
  const ButtonResponse: React.FC<Partial<IButtonProps>> = ({ onClick, children }) => {
    return (
      <button
        type="button"
        {...cLo("flex flex-shrink-0 hidden block w-6 h-6 p-1 mx-0.5 rounded-full",
          "dark:text-gray-500 dark:bg-gray-800 text-gray-600 border border-gray-400",
          "group-hover:block focus:outline-none",
          "dark:hover:text-gray-900 dark:hover:bg-gray-700",
          "hover:text-gray-600 hover:bg-gray-400 shadow-md")}
        onClick={onClick}
      >
        {children}
      </button>
    );
  }

  return (
    <>
      <ButtonResponse onClick={_e => console.log("Reply")}>
        <SvgIconReply />
      </ButtonResponse>
      <ButtonResponse onClick={_e => console.log("Smile")}>
        <SvgIconSmile />
      </ButtonResponse>
    </>
  );
}

const ChatBubble = ({ m, idSelf, isNewId, bdrClr }: {
  m:       IMsg,     // message
  idSelf:  boolean,  // sender is self
  isNewId: boolean,  // if the sender is different from the previous
  bdrClr:  string    // border color class
}) => {
  return (
    <div {...cCo(cC("text-sm grid grid-flow-row gap-1", idSelf,
                "dark:text-white text-black", "dark:text-gray-700 text-gray-800"),
      isNewId, 'mt-1', 'mt-0')}>
      <div {...cCo("group flex items-center", idSelf, "flex-row-reverse")}>
        <div className="flex flex-col">
          {m.text &&
          <div {...cCo(cL("max-w-xs px-2 py-2 rounded-xl lg:max-w-md border-b border-opacity-40", bdrClr),
                  idSelf,
                  "dark:bg-blue-700 bg-blue-200 rounded-tr-none border-r-2",
                  "dark:text-gray-200 dark:bg-gray-800 text-gray-800 bg-gray-50 rounded-tl-none border-l-4")}
            >
              {isNewId && <span className="font-mono dark:text-blue-300 text-blue-700">{m.sender + ' '}</span>}
              {m.text}
            </div>
          }
          {
          m.img &&
            <div {...cCo("flex items-center", idSelf, "flex-row-reverse", "flex-row")}>
              <a
                {...cCo(cL("max-w-xs my-2 lg:max-w-md", bdrClr), idSelf, 'border-r', 'border-l')}
                href={m.img}
                target="_blank"
                rel="noopener noreferrer"
              >
                <img src={m.img} alt="" width="100px" />
              </a>
            </div>
          }
        </div>
        <time
          {...cCo("mx-1 text-xs", idSelf,
            "dark:text-pink-200 text-pink-800", "dark:text-green-300 text-green-800")}
          dateTime={(new Date(m.time)).toISOString()}
        >
          {timeStr(m.time)}
        </time>
        {!idSelf && <ChatReactions />}
      </div>
    </div>
  );
}

export default ChatBubble;
