import { useAuthCtx } from 'app/AuthContext';
import {
  AuthForms,
  LogoutForm,
} from './AuthForms';
import { useFromPath }  from 'ui/AuthRoute';
import {
  ErrorDlg,
  PendingDlg,
} from 'ui/forms';
import {
  cLo,
} from 'utils/util';

const SpacetimeQText = () =>
  <span className="py-1 rounded-md">
    <span className="text-blue-400">Space</span>
    <span className="text-green-400">time</span>
    <span className="text-red-400">Q</span>
  </span>;

export default function AuthPage() {
  // const auth = useSelector(selectAuth);
  const fromPath = useFromPath();
  const auth = useAuthCtx();
  // console.log("AuthPage:", auth);

  return (
    <div>
      <h1 {...cLo("bg-gradient-to-r from-white via-black to-white",
        "text-lg text-center font-mono font-extrabold my-1")}
      >
        <SpacetimeQText />
        {fromPath && <div className="text-sm text-yellow-400">{fromPath}</div>}
      </h1>
      {
      auth.pending
      ? <PendingDlg />
      : auth.user
        ? <LogoutForm user={auth.user} />
        : <AuthForms />
      }
      {auth.error && <ErrorDlg error={auth.error} />}
    </div>
  );
}
