import { useState } from 'react';
import { NavLink } from 'react-router-dom';
import {
  UserMenu,
  UserMenuListPulldown
} from './UserMenu';
import {
  IButtonProps,
  IMenuItemClickProps,
  TButtonClickHandler
} from 'common';
import {
  cCo, cC0o, cLo, cL,
} from 'utils/util';
import { ToggleDarkButton } from 'ui/toggleDark';
import {
  getPathD,
  Svg6v24,
} from 'utils/svg';

// To connect components to the link, define them in Layout.tsx
const MenuLinks = [
  { to: "/",         title: <Svg6v24 d={getPathD("home_nav")} /> },
  { to: "/chat",     title: "Chat" },
  { to: "/calendar", title: "Calendar" },
  { to: "/timeline", title: "Timeline" },
];

// =========================================
// Tailwind Colorset Definitions
// =========================================
// colorset Background, Border
const tcsBgBorder = "dark:border-gray-600 dark:bg-gray-800 border-gray-800 bg-gray-200";
// colorset Text, Focus
const tcsTextFocus = cL(
  "dark:text-gray-300 dark:hover:text-white",
  "text-gray-700 hover:text-black",
  "dark:focus:outline-none dark:focus:text-white dark:focus:bg-gray-700",
  "focus:outline-none focus:text-black focus:bg-gray-300"
);
// colorset Text
const tcsText = "dark:text-gray-200 text-gray-800";
// =========================================

const MainMenuList = ({ mobile, onMenuItemClick }: {
  mobile?: Undefinable<boolean>,
} & IMenuItemClickProps) => {
  return (
    <div
      {...cC0o(mobile, cL("border-b border-t", tcsBgBorder), "flex flex-row")}
    >
      {MenuLinks.map(m =>
        <NavLink
          key={m.to}
          exact
          to={m.to}
          {...cLo("px-2 py-2 font-medium rounded-md opacity-70",
                  tcsTextFocus, mobile && "block ml-3")}
          activeStyle={{
            textDecoration: 'underline',
            opacity: 1
          }}
          onClick={onMenuItemClick}
        >
          {m.title}
        </NavLink>)}
    </div>
  );
}

// color doesn't change for the png file
const LogoButton = ({ onClick }: IButtonProps) => {
  return (
    <button
      {...cLo("p-2 mt-2 border-2 border-transparent rounded-full",
        "bg-gray-800 hover:bg-gray-700 focus:bg-gray-700")}
      aria-label="Logo"
      onClick={onClick}
    >
      <img className="w-6 h-6" src="logo.png" alt="" />
    </button>
  );
}

// << button
const SideArrowButton = ({ onClick }: IButtonProps) => {
  return (
    <button
      {...cLo("p-1 border-2 border-transparent rounded-full", tcsTextFocus)}
      aria-label="SideArrow"
      onClick={onClick}
    >
      <Svg6v24 d="M11 19l-7-7 7-7m8 14l-7-7 7-7" />
    </button>
  );
}

const NotiIconButton = ({ onClick }: IButtonProps) => {
  return (
    <button
      {...cLo("ml-2 p-2 border-transparent rounded-full", tcsTextFocus)}
      aria-label="Notifications"
      onClick={onClick}
    >
      <Svg6v24 d={getPathD("bell_noti")} />
    </button>
  );
}

// Create both on and off state svg and then switch the show and hide attribute only
// Make sure just the class= toggles not the <svg itself using the Inspector
// is Menu On (shown)?
const BurgerButton = ({ isOn, onClick }: { isOn: boolean; } & IButtonProps) => {
  const hideAttr = (hide: boolean) => hide ? 'hidden' : 'block';
  return (
    <button
      {...cLo("inline-flex items-center px-3 py-2 justify-center", tcsTextFocus)}
      onClick={onClick}
    >
      <Svg6v24 className={hideAttr(isOn)}  d="M4 6h16M4 12h16M4 18h16" />
      <Svg6v24 className={hideAttr(!isOn)} d="M6 18L18 6M6 6l12 12" />
    </button>
  );
}

// https://tailwindcss.com/docs/responsive-design
// Breakpoints (sm, md, lg, xl): takes effect at the specified bp and above
export default function Navbar() {
  const [mobileMenuOn, setMobileMenuOn] = useState(false);  // is Mobile Menu On
  const forDesktop = "hidden md:block";
  const forMobile  = "md:hidden";

  const onLogoClick: TButtonClickHandler = (_e) => {
    console.log("Logo clicked!");
  }

  const onSideArrowClick: TButtonClickHandler = (_e) => {
    console.log("Side Arrow clicked!");
  }

  const onNotificationClick: TButtonClickHandler = (_e) => {
    console.log("Notification clicked!");
  }
  
  return (
    <>
      <nav {...cLo("fixed z-20 w-full flex flex-col shadow-lg", tcsBgBorder)}>
        <div className="flex items-center justify-between Sq_navbar-h mx-2">

          <div {...cLo("flex items-center", tcsText)}>
            <div {...cLo("flex-shrink-0 mb-2", forDesktop)}>
              <div className="relative flex items-center justify-center">
                <LogoButton onClick={onLogoClick} />
              </div>
            </div>
            <div {...cLo("flex items-center", forMobile, tcsText)}>
              <SideArrowButton onClick={onSideArrowClick} />
            </div>
            <div className={forDesktop}>
              <MainMenuList onMenuItemClick={ _e => null } />
            </div>
          </div>

          <div className="flex items-center ml-4 md:ml-6">
            <ToggleDarkButton />
            <NotiIconButton onClick={onNotificationClick} />
            <div className={forDesktop}>
              <UserMenu />
            </div>
          </div>
          <div {...cLo("flex -mr-2", forMobile)}>
            <BurgerButton isOn={mobileMenuOn} onClick={_e => setMobileMenuOn(!mobileMenuOn)} />
          </div>

        </div>
        <div {...cCo(forMobile, mobileMenuOn, 'block', 'hidden')}>
          <MainMenuList mobile  onMenuItemClick={ _e => setMobileMenuOn(false) } />
          <UserMenuListPulldown onMenuItemClick={ _e => setMobileMenuOn(false) } />
        </div>
      </nav>
      <div className="Sq_navbar-h" />
    </>
  );
}
