import PATHD from 'styles/paths.json';
import { cLo } from './util';

// To avoid No Index Signature Error for the bracket (Obj[key])
export type TPathDataKey = keyof typeof PATHD;
export const getPathD = (k: TPathDataKey) => PATHD[k];

// <path> arguments
export const pathRules: React.SVGProps<SVGPathElement> = {
  fillRule: "evenodd",
  clipRule: "evenodd"
};

// <svg> arguments
export const viewBox = (vLen: number) => ({
  viewBox: `0 0 ${vLen} ${vLen}`
});

// short-hand for <svg width={len} height={len} viewBox=`0 0 ${vLen} ${vLen}`>
export const squareView = (len: number | string, vLen: number) => ({
  width:  len,
  height: len,
  ...viewBox(vLen)
});

interface ISvg6v24Props extends Partial<IClassNameObj> {
  d:            string;
  fill?:        string;
  stroke?:      string;
  strokeWidth?: number;
};
export const Svg6v24 = ({
  className,
  d,
  stroke      = "currentColor",
  fill        = "none",
  strokeWidth = 2
}: ISvg6v24Props
) =>
  <svg {...cLo("w-6 h-6", className)} {...{stroke, fill}} {...viewBox(24)}>
    <path strokeLinecap="round" strokeLinejoin="round" {...{strokeWidth, d}} />
  </svg>;
